* {
  margin: 0%;
  box-sizing: border-box;
}

a {
  text-decoration: none !important;
  font-weight: 500;
}

ul {
  padding-inline-start: 0%;
}

li {
  list-style: none;
}

[iframe] {
  border: none;
}

body {
  background-image: url("https://images.pexels.com/photos/36717/amazing-animal-beautiful-beautifull.jpg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
  background-size: cover;
  position: relative;
  color: white !important;
  font-family: "Lora", Helvetica, Arial, sans-serif !important;
  scroll-behavior: smooth;
  overflow: auto;
  animation: fadeInAnimation ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

.container {
  max-width: 1200px !important;
  padding: 0px 15px !important;
  margin: auto !important;
  position: relative;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

[class^="col-"] {
  padding: 0px 15px;
  flex: 1 1;
}

.col-1 {
  flex-basis: 8.33%;
}

.col-2 {
  flex-basis: 16.66%;
}

.col-3 {
  flex-basis: 25%;
}

.col-4 {
  flex-basis: 33.33%;
}

.col-5 {
  flex-basis: 41.66%;
}

.col-6 {
  flex-basis: 50%;
}

.col-7 {
  flex-basis: 58.33%;
}

.col-8 {
  flex-basis: 66.66%;
}

.col-9 {
  flex-basis: 75%;
}

.col-10 {
  flex-basis: 83.33%;
}

.col-11 {
  flex-basis: 91.66%;
}

.col-12 {
  flex-basis: 100%;
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgb(255, 255, 255);
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(0, 133, 82);
}
